import "./post.scss";
import FavoriteBorderOutlinedIcon from "@mui/icons-material/FavoriteBorderOutlined";
import FavoriteOutlinedIcon from "@mui/icons-material/FavoriteOutlined";
import TextsmsOutlinedIcon from "@mui/icons-material/TextsmsOutlined";
import ShareOutlinedIcon from "@mui/icons-material/ShareOutlined";
import MoreHorizIcon from "@mui/icons-material/MoreHoriz";
import { Link } from "react-router-dom";
import Comments from "../comments/Comments";
import { useState } from "react";
import moment from "moment";
import { useQuery, useQueryClient, useMutation } from "@tanstack/react-query";
import { makeRequest } from "../../axios";
import { useContext } from "react";
import { AuthContext } from "../../context/authContext";

const Post = ({post}) => {
    const [commentOpen, setCommentOpen] = useState(false)
    const [menuOpen, setMenuOpen] = useState(false);
    const {currentUser} = useContext(AuthContext)
    

    const { isLoading, error, data } = useQuery(["likes", post.id], () =>  // React-Query
  makeRequest.get("/likes?postId="+post.id).then((res) => { // makeRequest is in axios file through which we are connected to backend
    return res.data; // we get data from DB, basically likes
  })
  );

  const queryClient = new useQueryClient()

  const mutation = useMutation(
    (liked) =>{ 
        if(liked) // if it is liked already and we click on it
        {
            return makeRequest.delete("/likes?postId="+ post.id);
        }
        return makeRequest.post("/likes",{postId : post.id}) // if we like the post
    },
  {
    onSuccess: () => { 
      // Invalidate and refetch
      queryClient.invalidateQueries(["likes"]) 
    },
  });

  const deleteMutation = useMutation(
    (postId) =>{ 
        return makeRequest.delete("/posts/" + postId) 
    },
  {
    onSuccess: () => { 
      // Invalidate and refetch
      queryClient.invalidateQueries(["posts"]) 
    },
  });

  const handleLike = () => {
    mutation.mutate(data.includes(currentUser.id))

  }

  const handleDelete = () => {
    deleteMutation.mutate(post.id)
  }


  return (
    <div className="post">

        <div className="container">

            <div className="user">
                <div className="userInfo">
                    <img src={"/upload/"+post.profilePic} alt="" />
                    <div className="details">
                        <Link to={`/profile/${post.userId}`} style={{textDecoration:"none", color:"inherit"}}>
                            <span className="name">{post.name}</span>
                        </Link>
                        <span className="date">{moment(post.createdAt).fromNow()}</span>
                    </div>
                </div>
                <MoreHorizIcon onClick={() => setMenuOpen(!menuOpen)}/> { /* In below line if menuOpen is true and post's userid = currentuserid, then we will show button*/}
                {menuOpen && post.userId === currentUser.id && (  
                  <button onClick={handleDelete}>delete</button>
                )} 
            </div>
            <div className="content">
                <p>{post.desc}</p>
                <img src={"./upload/" + post.img} alt="" />
            </div>
            <div className="info">

            <div className="item">
                {isLoading ? (
                  "loading"
                ) : data.includes(currentUser.id) ? (
                  <FavoriteOutlinedIcon
                    style={{ color: "red" }}
                    onClick={handleLike}
                  />
                ) : (
                  <FavoriteBorderOutlinedIcon onClick={handleLike} />
                )}
                {data?.length} Likes
            </div>
                <div className="item" onClick={() => setCommentOpen(!commentOpen)}> {/* kind of toggling the commentOpen */}
                    <TextsmsOutlinedIcon/>
                     Comments
                </div>
                <div className="item">
                    <ShareOutlinedIcon/>
                    Share
                </div>

            </div>
            {commentOpen && <Comments postId={post.id}/>}  {/* If commentOpen is true it will show comments */}

        </div>
        
    </div>
  )
}

export default Post;
