import Login from "./pages/login/Login"
import Register from "./pages/register/Register";
import {
  createBrowserRouter,
  RouterProvider,
  // Route,
  Navigate,
  Outlet,
} from "react-router-dom";
import Navbar from "./components/Navbar/Navbar";
import LeftBar from "./components/leftBar/LeftBar";
import RightBar from "./components/rightBar/RightBar";
import Home from "./pages/home/Home";
import Profile from "./pages/profile/Profile";
import "./style.scss";
import { useContext } from "react";
import { DarkModeContext } from "./context/darkModeContext";
import { AuthContext } from "./context/authContext";
import { QueryClient,QueryClientProvider } from '@tanstack/react-query'


function App() {
  const {currentUser} = useContext(AuthContext); // User authentication

  const {darkMode} = useContext(DarkModeContext);

  const queryClient = new QueryClient() //react query

  const Layout = ()=>{
    return(
      <QueryClientProvider client={queryClient}> {/* we bind it so that we will be able to reach any react-query with the help of it */}
        <div className={`theme-${darkMode ? "dark" : "light"}`}> {/*Check the darkMode if its dark then it will write here dark otherwise light */}
          <Navbar/>
          <div style={{display: "flex"}}>
            <LeftBar />

            <div style={{flex:6}}>
              <Outlet />  {/* It Varies */}
            </div>

            <RightBar />
          </div>
        </div>
      </QueryClientProvider>
    )
  } 
  const ProtectedRoute = ({ children }) => { // children here is the protected "Layout", our home page and profile page
    if (!currentUser) {
      
      return <Navigate to="/login" />;
    }

    return children;
  };

  const router = createBrowserRouter([
    {
      path: "/",  // home route
      element: (
        <ProtectedRoute>
          <Layout />
        </ProtectedRoute>
      ),
      children: [
        {
          path: "/",
          element: <Home />,
        },
        {
          path: "/profile/:id", // this id can be any number or string
          element: <Profile />,
        },
      ],
    },
    {
      path: "/login",
      element: <Login />,
    },
    {
      path: "/register",
      element: <Register />,
    },
  ]);

  return (
    <div>
      <RouterProvider router={router} />
    </div>
  );
}

export default App;
